<template>
    <div @click="play" :style="style" ref="lavContainer"></div>
</template>

<script>
  import lottie from 'lottie-web';

  export default {
    props: {
      options: {
        type: Object,
        required: true
      },
      height: Number,
      width: Number,
      playType: String,
    },

    data () {
      return {
        style: {
          width: this.width ? `${this.width}px` : '100%',
          height: this.height ? `${this.height}px` : '100%',
          overflow: 'hidden',
          
        },
        anim :{}
      }
    },

    mounted () {
      this.anim = lottie.loadAnimation({
          container: this.$refs.lavContainer,
          renderer: 'svg',
          loop: this.options.loop !== false,
          autoplay: this.options.autoplay !== false,
          animationData: this.options.animationData,
          rendererSettings: this.options.rendererSettings
        }
      );
    },
    created: function() {
        if(this.playType == 'once'){
            this.options.loop = false,
            this.options.autoplay = false
        }else{
          this.options.loop = true,
            this.options.autoplay = true
        }
       },
     methods: {
      play: function () {
        this.anim.play();
      },

    },
    watch: {
      'anim.currentFrame': function(){
        var nowpercent = this.anim.currentFrame/this.anim.totalFrames*100;

        if(this.playType == 'once'){
          if(nowpercent >= 50 && nowpercent <51){
            this.anim.pause();
            nowpercent = nowpercent +1
          }
          if(nowpercent >= 98 ){
            this.anim.stop();
        }
        }
      },
    },
  }
</script>
